import React, { Component } from 'react';
import Collapsepage from './Collapsepage'
class Maps extends Component {
 constructor(props) {
   super(props);

   this.state = {
    url:null
   };
 
 }

 componentWillMount() {
    if(navigator.geolocation){
        navigator.geolocation.getCurrentPosition(this.showPosition)
      }
  }

 showPosition = (position)=>{
    const lat = position.coords.latitude;
    const long = position.coords.longitude;
    const url = `https://image.maps.api.here.com/mia/1.6/routing?app_id=uXKmufo4aXIXBS5nw4HA&app_code=kQg_zysltomrOggVDhzuvw&waypoint0=${lat},${long}&waypoint1=19.415167,-99.163384&poix0=${lat},${long};00a3f2;00a3f2;11;.
    &poix1=19.415167,-99.163384;white;white;11;.&lc=1652B4&lw=6&t=0&ppi=320&w=400&h=600`
    this.setState({url})
}

render (){
return(
    <div className="container">
          <img className="responsive" src={this.state.url} alt='mapa'/>
            <Collapsepage />
        </div>
)

}
}

export default Maps
